body {
  font-size: 32px;
  margin: 0;
  font-family: 'Chicago';
  -webkit-font-smoothing: none;
  background-color: #FFF;
  background-size: 4px 4px;
  background-position: 0 0, 2px 2px;
  background-image: linear-gradient(45deg, black 25%, transparent 25%, transparent 75%, black 75%, black),
                    linear-gradient(45deg, black 25%, transparent 25%, transparent 75%, black 75%, black);
}

.apple-logo {
  width: 30px;
  height: 30px;
  margin-top: 2px;
  background-image:url(
    data:image/png;base64,
  );
  background-repeat: no-repeat;
}

::selection {
  background: black;
  color: white;
}

/* width */
::-webkit-scrollbar {
  width: 32px;
  border-left: 2px solid black;
  display: block;
}

::-webkit-scrollbar-button {
  position: absolute;
  display: block;
  border: 2px solid black;
  height: 32px;
}

::-webkit-scrollbar-button:vertical:start:decrement {
  background-image:url(
    data:image/png;base64,
  )
}

::-webkit-scrollbar-button:vertical:end:increment {
  background-image:url(
    data:image/png;base64,
  );
}


::-webkit-scrollbar-button:vertical:start:increment,
::-webkit-scrollbar-button:vertical:end:decrement,
::-webkit-scrollbar-button:horizontal:start:increment,
::-webkit-scrollbar-button:horizontal:end:decrement {
  width: 0;
  height: 0;
}

/* Track */
::-webkit-scrollbar-track {
  border-left: 2px solid black;
  background-size: 4px 4px;
  background-position: 0 0, 6px 6px;
  background:url(
    data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAQAAAAECAYAAACp8Z5+AAAAJElEQVQYV2NkYGD4z8DAwMjIAAFgBlgEJgAWhXD+/0dSCVcBAJpfBAjuj7LFAAAAAElFTkSuQmCC
  ) repeat;
}

/* Handle */
::-webkit-scrollbar-thumb {
  border-left: 2px solid black;
  border: 2px solid black;
  height: 32px;
  background-color: white;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  height: 32px;
}

@font-face {
  font-family: 'Chicago';
  src: URL(/system-1-demo/static/media/ChiKareGo2.e6bedc71.ttf) format('truetype');
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  width: 100vw;
  height: 100vh;

}

.message-container { 
  margin: auto;
  border: 2px solid black;
  width: 320px;
  max-height: 320px;
  background: white;
  left: 10%;
}

main {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 230px;
  margin-top: 2px;
  border-top: 2px solid black;
}

header {
  border-bottom: 2px solid black;
  padding-top: 4px;
  padding-bottom: 5px;
  padding-left: 2px;
  padding-right: 2px;
  line-height: 26px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

header .background {
  height: 24px;
  background: repeating-linear-gradient(
    white,
    white 2px, 
    black 2px,
    black 4px
  );
}

header span {
  padding-left: 8px;
  padding-right: 4px;
  background-color: #FFF;
}

input, button { 
  -webkit-appearance: none;
}
:focus {
  outline:none
}

